/* Fullscreen wrapper: when active, shows compact search on top and map below */
.map-view-fullscreen-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 400px;
  display: flex;
  flex-direction: column;
}

.map-view-fullscreen-wrapper .map-view-fullscreen-search {
  flex-shrink: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: height 0.2s ease, opacity 0.2s ease;
}

.map-view-fullscreen-wrapper.fullscreen-active .map-view-fullscreen-search {
  height: auto;
  overflow: visible;
  opacity: 1;
  flex-shrink: 0;
}

.map-view-fullscreen-wrapper .map-wrapper {
  flex: 1;
  min-height: 0;
}

/* Map Container Styles */
.map-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 400px;
}

.map-container {
  width: 100%;
  height: 100%;
  border-radius: 12px;
  overflow: hidden;
}

/* Loading State */
.map-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9);
  z-index: 10;
  border-radius: 12px;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid var(--color-border);
  border-top-color: var(--color-primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Custom Price Tag Markers - Small Pill-Shaped */
.price-tag-marker {
  cursor: pointer;
  transition: transform 0.2s ease;
}

.price-tag-marker:hover {
  transform: scale(1.1);
  z-index: 10;
}

.price-tag {
  background: var(--color-primary);
  color: var(--color-text-white);
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  white-space: nowrap;
  box-shadow: var(--shadow-card);
  transition: transform 0.2s ease;
  cursor: pointer;
  display: inline-block;
  min-width: auto;
  width: auto;
}

.price-tag:hover {
  transform: scale(1.1);
}

/* Selected/Current Property Marker - Orange */
.price-tag-marker.selected .price-tag {
  background: var(--color-warning, #F97316);
  z-index: 100;
}

.price-tag-marker.selected:hover {
  transform: scale(1.1);
  z-index: 10;
}

/* ========== POPUP STYLES - VERTICAL CARD LAYOUT ========== */

/* Map Card Container - Responsive sizing */


.mapboxgl-popup.property-map-popup-container {
  max-width: 380px !important;
  width: auto !important;
  min-width: 320px;
  max-height: 450px !important;
}

.mapboxgl-popup-content {
  padding: 0;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: var(--shadow-card-hover);
  background: var(--color-surface);
  display: flex;
  flex-direction: column;
  height: 100%;
}

.mapboxgl-popup-close-button {
  display: none;
  /* Hide default close button, using custom one */
}

/* Property Popup Card - Vertical Layout (Image Top, Content Bottom) */
.property-popup-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  max-height: 450px;
  background: var(--color-surface);
  overflow: hidden;
  box-sizing: border-box;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.property-popup-card:hover {
  transform: translateY(-2px);
}

.popup-card-image-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  background: var(--color-bg);
  flex: 0 0 65%;
  min-height: 200px;
  max-height: 270px;
}

.popup-card-image {
  width: 100%;
  height: 100%;
  min-height: 200px;
  object-fit: cover;
  object-position: center;
  display: block;
}

/* Image Overlay - Heart, Share and Close buttons */
.popup-card-image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 12px;
  pointer-events: none;
  z-index: 2;
}

.popup-card-action-buttons {
  display: flex;
  gap: 8px;
  align-items: flex-start;
}

.popup-card-heart-btn,
.popup-card-share-btn,
.popup-card-close-btn {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  pointer-events: auto;
  transition: all 0.2s ease;
  color: var(--color-text-white);
  padding: 0;
}

.popup-card-heart-btn:hover,
.popup-card-share-btn:hover,
.popup-card-close-btn:hover {
  background: rgba(0, 0, 0, 0.7);
  transform: scale(1.1);
}

.popup-card-heart-btn.active {
  background: rgba(220, 38, 38, 0.8);
}

.popup-card-heart-btn.active:hover {
  background: rgba(220, 38, 38, 1);
}

.popup-card-heart-btn svg,
.popup-card-share-btn svg,
.popup-card-close-btn svg {
  width: 18px;
  height: 18px;
}

/* Pagination Dots */
.popup-card-pagination {
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 6px;
  z-index: 3;
  pointer-events: none;
}

.pagination-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  transition: all 0.2s ease;
  cursor: pointer;
  pointer-events: auto;
}

.pagination-dot.active {
  background: var(--color-text-white);
  width: 8px;
  height: 8px;
}

/* Carousel Arrows */
.popup-card-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.4);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  pointer-events: auto;
  transition: all 0.2s ease;
  z-index: 4;
  padding: 0;
  opacity: 0;
}

.popup-card-image-container:hover .popup-card-arrow {
  opacity: 1;
}

.popup-card-arrow:hover {
  background: rgba(0, 0, 0, 0.7);
  transform: translateY(-50%) scale(1.1);
}

.popup-card-arrow-prev {
  left: 10px;
}

.popup-card-arrow-next {
  right: 10px;
}

.popup-card-arrow svg {
  width: 20px;
  height: 20px;
  color: white;
}

/* Content Section */
.popup-card-content {
  padding: 10px 14px 12px 14px;
  display: flex;
  flex-direction: column;
  gap: 0;
  flex: 0 0 35%;
  min-height: 0;
  overflow-y: auto;
  justify-content: flex-start;
  box-sizing: border-box;
}

.popup-card-header {
  display: flex;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}

.popup-card-title {
  font-size: 15px;
  font-weight: 700;
  color: var(--color-text-primary);
  margin: 0;
  padding: 10px 0 3px 0;
  border-top: 1px solid var(--color-border);
  line-height: 1.3;
  flex: 1;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  letter-spacing: -0.2px;
  text-transform: uppercase;
}

.popup-card-description {
  font-size: 12px;
  color: var(--color-text-secondary);
  margin: 0;
  padding: 3px 0 0 0;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.popup-card-price-section {
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 0;
  margin: 8px 0 0 0;
  padding: 0;
}

.popup-card-price {
  font-size: 18px;
  font-weight: 700;
  color: var(--color-text-primary);
  margin: 0;
  padding: 0;
  letter-spacing: -0.3px;
}

.popup-card-view-details-btn {
  width: 100%;
  margin: 14px 0 0 0;
  padding: 11px 18px;
  background: var(--color-primary-dark, #003B73);
  color: var(--color-text-white);
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s ease;
  text-transform: none;
  letter-spacing: 0;
  box-shadow: var(--shadow-card);
  flex-shrink: 0;
  white-space: nowrap;
  box-sizing: border-box;
}

.popup-card-view-details-btn:hover {
  background: var(--color-primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-card-hover);
}

.popup-card-view-details-btn:active {
  transform: translateY(0);
  box-shadow: var(--shadow-card);
}

.popup-card-view-details-btn svg {
  width: 16px;
  height: 16px;
  transition: transform 0.2s ease;
}

.popup-card-view-details-btn:hover svg {
  transform: translateX(2px);
}

/* Mapbox Controls Customization */
.mapboxgl-ctrl-group {
  border-radius: 8px !important;
  box-shadow: var(--shadow-card) !important;
}

.mapboxgl-ctrl-group button {
  width: 36px !important;
  height: 36px !important;
}

.mapboxgl-ctrl-geolocate {
  background-color: var(--color-surface);
}

/* ========== TABLET RESPONSIVE (768px - 1024px) ========== */
@media (max-width: 1024px) {


  .popup-card-content {
    padding: 14px 16px 16px 16px;
    gap: 8px;
  }

  .popup-card-title {
    font-size: 16px;
    padding-top: 12px;
  }

  .popup-card-description {
    font-size: 12px;
  }

  .popup-card-price {
    font-size: 18px;
  }

  .popup-card-view-details-btn {
    padding: 8px 14px;
    font-size: 13px;
    margin-top: 10px;
    flex-shrink: 0;
  }

  .popup-card-view-details-btn svg {
    width: 14px;
    height: 14px;
  }

  .popup-card-heart-btn,
  .popup-card-close-btn {
    width: 28px;
    height: 28px;
  }

  .popup-card-heart-btn svg,
  .popup-card-close-btn svg {
    width: 16px;
    height: 16px;
  }
}

/* ========== MOBILE RESPONSIVE (< 768px) ========== */
@media (max-width: 768px) {
  .map-wrapper {
    min-height: 300px;
  }



  .mapboxgl-popup-content {
    border-radius: 10px;
  }

  .popup-card-image-container {
    aspect-ratio: 16 / 11;
  }

  .popup-card-image-overlay {
    padding: 10px;
  }

  .popup-card-heart-btn,
  .popup-card-close-btn {
    width: 26px;
    height: 26px;
  }

  .popup-card-heart-btn svg,
  .popup-card-close-btn svg {
    width: 14px;
    height: 14px;
  }

  .popup-card-pagination {
    bottom: 10px;
    gap: 5px;
  }

  .pagination-dot {
    width: 5px;
    height: 5px;
  }

  .pagination-dot.active {
    width: 7px;
    height: 7px;
  }

  .popup-card-content {
    padding: 12px 12px 14px 12px;
    gap: 6px;
  }

  .popup-card-title {
    font-size: 14px;
    -webkit-line-clamp: 2;
  }

  .popup-card-description {
    font-size: 11px;
  }

  .popup-card-price {
    font-size: 16px;
  }

  .popup-card-view-details-btn {
    padding: 8px 12px;
    font-size: 12px;
    margin-top: 8px;
    flex-shrink: 0;
  }

  .popup-card-view-details-btn svg {
    width: 12px;
    height: 12px;
  }

  /* Smaller price tags on mobile */
  .price-tag {
    padding: 5px 10px;
    font-size: 11px;
  }
}

/* ========== SMALL MOBILE (< 480px) ========== */
@media (max-width: 480px) {

  .popup-card-image-container {
    aspect-ratio: 16 / 12;
  }

  .popup-card-image-overlay {
    padding: 8px;
  }

  .popup-card-heart-btn,
  .popup-card-close-btn {
    width: 24px;
    height: 24px;
  }

  .popup-card-heart-btn svg,
  .popup-card-close-btn svg {
    width: 12px;
    height: 12px;
  }

  .popup-card-pagination {
    bottom: 8px;
    gap: 4px;
  }

  .popup-card-content {
    padding: 10px 10px 12px 10px;
    gap: 5px;
  }

  .popup-card-title {
    font-size: 13px;
  }

  .popup-card-description {
    font-size: 10px;
  }

  .popup-card-price {
    font-size: 15px;
  }

  /* Even smaller price tags */
  .price-tag {
    padding: 4px 8px;
    font-size: 10px;
  }
}

/* ========== EXTRA SMALL MOBILE (< 360px) ========== */
@media (max-width: 360px) {

  .popup-card-image-container {
    aspect-ratio: 16 / 13;
  }

  .popup-card-content {
    padding: 8px 8px 10px 8px;
    gap: 4px;
  }

  .popup-card-title {
    font-size: 12px;
  }

  .popup-card-description {
    font-size: 9px;
  }

  .popup-card-price {
    font-size: 14px;
  }

  .popup-card-duration {
    font-size: 10px;
  }

  .popup-card-dates {
    font-size: 9px;
  }

  .price-tag {
    padding: 3px 6px;
    font-size: 9px;
    border-radius: 12px;
  }
}

.mapboxgl-canvas {
  left: 0;
  position: absolute;
  top: 0;
  width: 100% !important;
}